


.root {
    width: 250px;
    border-right: 1px solid var(--dividing-border-color);
    height: 100%;
}

.header {
    padding: 20px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid var(--dividing-border-color);
}

.login {
    font-size: 18px;
    font-weight: 500;
    width: 105px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.list {
    padding: 40px 30px;
    display: flex;
    flex-direction: column;
    gap: 20px
}

.item {
  
    a {
        border-radius: 10px;
        font-size: 20px;
        font-weight: 600;
    }

    &__active {
        color: purple;
    }

}