


.root {
    padding: 40px;
    width: 100%;
    max-width: 500px;
    height: 450px;
    background-color: rgba(172, 167, 167, 0.429);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
}

.title {
    font-size: 40px;
    font-weight: 600;
    text-align: center;
}

.formItems {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.formItem {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.submitBtn {
    margin-top: auto;
}

.formChange {
    margin-top: 30px;
    cursor: pointer;
    color: blue;
    text-align: center;
}