


.root {
    :global {
        .ant-table-row {
            cursor: pointer;
        }

        .ant-table-cell {
            vertical-align: middle;
            height: 55px !important;
            padding: 0 16px!important;
        }
    }
}

.header {
    height: 90px;
    padding: 20px 40px;
    border-bottom: 1px solid var(--dividing-border-color);
    display: flex;
    align-items: center;
}

.filters {
    display: flex;
    justify-content: space-between;
}

.search {
    max-width: 250px;
}

.sortWrapper {
    display: flex;
    align-items: center;
    gap: 10px;
    
    p {
        font-weight: 600;
    }
}

.sort {
    width: 200px;
}

.btnsWrapper {
    margin-left: auto;
    display: flex;
    gap: 10px;
}

.content {
    padding: 20px;
    overflow-y: scroll;
}

.table {
    margin-top: 20px;
}

.imgHolder {
    display: flex;
    height: 100%;
    width: max-content;
    align-items: center;

    img {
        border-radius: 5px;
        overflow: hidden;
    }
}