


.root {
    :global {
        .ant-table-row {
            cursor: pointer;
        }

        .ant-table-cell {
            vertical-align: middle;
            height: 55px !important;
            padding: 0 16px!important;
        }
    }
}

.header {
    height: 90px;
    padding: 20px 40px;
    border-bottom: 1px solid var(--dividing-border-color);
    display: flex;
    align-items: center;
}


.content {
    padding: 20px;
    overflow-y: scroll;
}

.table {
    margin-top: 20px;
}

