@import "../styles/reset";
@import "../styles/mixins/breakpoints";
@import "../styles/variables/theme";


html,
body {
    position: relative;
    max-width: 100vw;
    min-width: 1200px;
    overflow-x: auto;
}

main {
    grid-column: 2;
    width: 100%;
    overflow: hidden;
    min-height: 100vh;
}

.layout {
    display: grid;
    grid-template-columns: 250px 1fr;
    border-bottom: 1px solid var(--dividing-border-color);
}

h1 {
    font-size: 32px;
    font-weight: 600;
}

h2 {
    font-size: 24px;
    line-height: 32px;
    font-weight: 600;
}

.ant-modal-content {
    width: 1160px;
}

@include breakpoint(xl) {
    html,
    body {
        min-width: unset;
        overflow-x: unset;
   }

   .ant-modal-content {
    width: unset;
   }
}