



.header {
    height: 90px;
    padding: 20px 40px;
    border-bottom: 1px solid var(--dividing-border-color);
    display: flex;
    align-items: center;
    gap: 20px;

    button {
        margin-left: auto;
    }

}

.returnLink {
    padding: 10px 5px;
   display: flex;
   justify-content: center;
   align-items: center;
   background: rgba(128, 128, 128, 0.1);
   border-radius: 6px;
   svg {
    height: 25px !important;
    width: auto !important;
   }
}

.form {
    padding: 20px;
    padding-bottom: 100px;
    height: calc(100vh - 90px);
    overflow-y: scroll;
}

.formItems {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: max-content;
    gap: 20px;
    column-gap: 40px;
}

.formTitle {
    padding-bottom: 10px;
    grid-column: 1/3;
}

.formItem {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.error {
    color: red;
    font-size: 14px;
}