



.root {
    :global {
        
        .ant-modal-title {
            font-size: 28px;
        }

        .ant-checkbox {
            transform: scale(1.5);
        }

        .ant-modal-content {
            height: 97vh;
            display: flex;
            flex-direction: column;
        }

        .ant-modal-body {
            flex: 1;
            overflow-y: auto;
            scrollbar-width: thin;
        }
    }
}



.content {
    padding: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
}

.filters {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.search {
    max-width: 250px;
}

.checkAll {
    display: flex;
    align-items: center;
    gap: 10px;
}


.sortWrapper {
    display: flex;
    align-items: center;
    gap: 10px;
    
    p {
        font-weight: 600;
    }
}

.sort {
    width: 200px;
}

.list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-auto-rows: 250px;
    justify-content: flex-start;
    gap: 10px;
}

.item {
    position: relative;
    border-radius: 10px;
    border: 1px solid var(--dividing-border-color);
    overflow: hidden;

    p {
        padding: 5px;
        font-size: 14px !important;
        line-height: 18px;
        width: 150px;
        height: 40px;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

.checkbox {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 10;
}

.imageHolder {
    width: 100%;
    height: calc(100% - 40px);
    overflow: hidden;
}

.image {
    object-fit: cover !important;
    object-position: center !important;
}



.pagination {
    margin-left: auto;
}