


.root {
    :global {
        .ant-input-number {
            width: 100%;
        }
        .ant-checkbox {
            transform: scale(1.5);
        }
    }
}

.header {
    height: 90px;
    padding: 20px 40px;
    border-bottom: 1px solid var(--dividing-border-color);
    display: flex;
    align-items: center;
    gap: 20px;

    button {
        margin-left: auto;
    }

}

.returnLink {
    padding: 10px 5px;
   display: flex;
   justify-content: center;
   align-items: center;
   background: rgba(128, 128, 128, 0.1);
   border-radius: 6px;
   svg {
    height: 25px !important;
    width: auto !important;
   }
}

.form {
    padding: 20px;
    padding-bottom: 100px;
    height: calc(100vh - 90px);
    overflow-y: scroll;
}

.formItems {
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: max-content;
    gap: 20px;
    column-gap: 40px;
}

.title {
    grid-column: span 4;
}


.formItem {
    grid-column: span 2;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.number, .date, .checkbox {
    grid-column: span 1;
}

.checkbox {
align-items: center;
gap: 20px;
}

.images {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.imagesList {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.imageHolder {
    border-radius: 10px;
    overflow: hidden;
    width: 120px;
    height: 120px;
    position: relative;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}

.addImagesBtn {
    margin: auto;
}

.deleteImageBtn {
    position: absolute;
    top: 5px;
    right: 5px;
    background: red;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    z-index: 1;
    
    svg {
        position: absolute;
        top: -4px;
        right: -4px;
        fill: white;
        height: 30px;
        width: auto;
        z-index: 2;
    }
    
    }

    .error {
        color: red;
        font-size: 14px;
    }