



.root {

    :global {
        .ant-input-number, .ant-select {
            width: 100%;
        }

        .ant-checkbox {
            transform: scale(1.5) !important;
        }
    }
 
}

.title {
    max-width: 800px;
    overflow: hidden;
    text-overflow: ellipsis;
    text-wrap: nowrap;
}

.header {
    height: 90px;
    padding: 20px 40px;
    border-bottom: 1px solid var(--dividing-border-color);
    display: flex;
    align-items: center;
    gap: 20px;
}

.btnsWrapper {
    margin-left: auto;
    display: flex;
    gap: 10px;
}

.returnLink {
    padding: 10px 5px;
   display: flex;
   justify-content: center;
   align-items: center;
   background: rgba(128, 128, 128, 0.1);
   border-radius: 6px;
   svg {
    height: 25px !important;
    width: auto !important;
   }
}

.form {
    padding: 20px;
    padding-bottom: 100px;
    height: calc(100vh - 90px);
    overflow-y: scroll;
}

.formItems {
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: max-content;
    gap: 20px;
    column-gap: 40px;
}


.formItem {
    grid-column: span 2;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.error {
    color: red;
    font-size: 14px;
}

.images {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.name {
    grid-column: span 2;
}

.number {
    grid-column: span 1;
}

.stock, .price {
    grid-column: span 1;
}

.description, .whereToShow {
    grid-column: span 4;
}

.new {
    width: 100%;
    grid-column: span 1;
    align-items: center;
    display: flex;
    gap: 20px;
}

.checkboxesWrapper {
    display: flex;
    gap: 50px;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.checkboxHolder {
    display: flex;
    gap: 15px;
    align-items: center;
}

.imagesList {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.imageHolder {
    border-radius: 10px;
    overflow: hidden;
    width: 120px;
    height: 120px;
    position: relative;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}


.addPreviewBtn {
    margin: auto;
}

.addImagesBtn {
    margin: auto;
}

.deleteImageBtn {
position: absolute;
top: 5px;
right: 5px;
background: red;
width: 22px;
height: 22px;
border-radius: 50%;
z-index: 1;

svg {
    position: absolute;
    top: -4px;
    right: -4px;
    fill: white;
    height: 30px;
    width: auto;
    z-index: 2;
}
}

.features {
    margin-top: 20px;
}

.featuresSelect {
    margin-top: 20px;
}