



.root {
    :global {
        .ant-checkbox {
            transform: scale(1.5);
        }
    }
}

.header {
    height: 90px;
    padding: 20px 40px;
    border-bottom: 1px solid var(--dividing-border-color);
    display: flex;
    align-items: center;
}

.btnsWrapper {
    margin-left: auto;
    display: flex;
    gap: 10px;
}

.content {
    padding: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
}

.filters {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.search {
    max-width: 250px;
}

.checkAll {
    display: flex;
    align-items: center;
    gap: 10px;
}

.sortWrapper {
    display: flex;
    align-items: center;
    gap: 10px;
    
    p {
        font-weight: 600;
    }
}

.sort {
    width: 200px;
}

.list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: 250px;
    justify-content: flex-start;
    gap: 10px;
}

.item {
    position: relative;
    border-radius: 10px;
    border: 1px solid var(--dividing-border-color);
    overflow: hidden;
}

.checkbox {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 10;
}

.imageHolder {
    width: 100%;
    height: calc(100% - 40px);
    overflow: hidden;
}

.image {
    object-fit: cover !important;
    object-position: center !important;
}

.title {
    width: 100%;
    padding: 5px;
    font-size: 14px;
    margin-top: 5px;
    height: 40px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.pagination {
    margin-left: auto;
}