


.root {

    :global {
        .ant-table-row {
            cursor: pointer;
        }
    }
}

.header {
    height: 90px;
    padding: 20px 40px;
    border-bottom: 1px solid var(--dividing-border-color);
    display: flex;
    align-items: center;
}

.btnsWrapper {
    margin-left: auto;
    display: flex;
    gap: 10px;
}

.content {
    padding: 20px;
    overflow-y: scroll;
}

.filters {
    display: flex;
    justify-content: space-between;
}

.search {
    max-width: 250px;
}

.sortWrapper {
    display: flex;
    align-items: center;
    gap: 10px;
    
    p {
        font-weight: 600;
    }
}

.sort {
    width: 200px;
}

.table {
    margin-top: 20px;
}